<template>
<div>
<ul class="moreMovieUl">
<li v-for="item in subject" class="moreMovieLi">
<a>
<img v-bind:src="item.images.medium" alt="" />
<span class="movieTitle">{{item.title}}</span>
<span class="movieRating">:<i>{{item.rating.average}}</i></span>
</a>
</li>
</ul>
</div>
</template>


<script>
 import jsonp from 'jsonp'
  import Vue from 'vue'

 export default{
     name:"app",
     data(){
         return{
             subject:""
         }
     },
     created (){
         this.getJsonp()
     },
     methods:{
          getJsonp () {
           self=this
        jsonp('https://api.douban.com/v2/movie/in_theaters', null, function (err, data) {
          if (err) {
            console.error(err.message)
          } else {
         
              self.subject=data.subjects
           
              console.log(self.subject)
            }
               
             
        })
      }
     }
 }

</script>



<style>
.moreMovieUl{
    width:100%;
}

.moreMovieLi{
   width:30%;
   height:16rem;
   margin:1rem 0 0 3.3%;
   box-sizing:border-box;
   float:left;
  
}
.moreMovieLi a{
    display:block;
    width:100%;
    height:100%;
}
.moreMovieLi img{
    width:100%;
    height:12rem;
}
.movieTitle{
    display:block;
    width:100%;
    height:2rem;
    float:left;
    font-size:1rem;
    color:black;
}
.movieRating{
    display:block;
    width:100%;
    height:1rem;
    float:left;
    font-size:1rem;
    color:black;
}

</style>


