<template>
  <swiper :options="swiperOption" ref="mySwiper" class="swiper1">
    <!-- slides -->
    <swiper-slide style=" border:1px solid pink;"><a></a> </swiper-slide>
    <swiper-slide style=" border:1px solid green;"><a></a> </swiper-slide>
    <swiper-slide style=" border:1px solid orange;"><a>2015</a> </swiper-slide>
    <swiper-slide style=" border:1px solid blue;"><a>100</a> </swiper-slide>
  </swiper>  
</template>
 
<script>
  import jsonp from 'jsonp'
  import Vue from 'vue'

  export default {
    name: 'carrousel2',
    data() {
      return {
        swiperOption: {
         height:100,
          notNextTick: true,
          autoplay: 3000,
          direction : 'horizontal',
          grabCursor : true,
          setWrapperSize :true,
          autoHeight: true,
          paginationClickable :true,
          mousewheelControl : true,
          observeParents:true,
          slidesPerView : 2,
          debugger: true,
          spaceBetween : 20,
          onTransitionStart(swiper){
          },    
        }
        
      }
    },
    
    computed: {
      swiper() {
        return this.$refs.mySwiper.swiper
      }
    },
    mounted() {
      this.swiper.slideTo(3, 1000, false)
    }
    
  }


  
</script>

<style>
.swiper-slide a{
  display:block;
  height:3rem;
 
}
.swiper-slide{
  height:4rem;
  float:left;
  margin-top:1rem;
 
  
}
</style>


