  <template>
  <div>
  <div class="homeSection">
  <h4>{{data.title}}</h4>
  <p>{{data.summary}}</p>
  <img v-bind:src="data.images" alt=""/>
  </div>
  </div>
</template>
 
<script>
  import jsonp from 'jsonp'
  import Vue from 'vue'

  export default {
    name: 'carrousel',
    data() {
      return {
         data:" "
      }
    },
     created () {
      this.getJsonp()
    },
     methods: {
      getJsonp () {
          self=this
        jsonp('https://api.douban.com/v2/note/629718319', null, function (err, data) {
          if (err) {
            console.error(err.message)

          } else {
              self.data=data
              console.log(data)
          }
        });
      }
    }
  }


  
</script>

<style>
.homeSection{
    width:100%;
    height:4rem;
    float:left;
}
</style>


