<template>
  <div class="searchMovie">
  <div class="searchHeader">
  <form action="" id="demo">
    <input type="text" v-model="searchName" class="searchName">
    <a href="javascript:void(0)" v-on:click="Menter" style="color:black;"></a>
  </form>
  </div>
  <div>
    
    <div></div>
   <div class="searchResult">
   <ul class="searchResultUL">
     <li v-for="item in data" class="lazy">
      <a>
     <img v-bind:src="item.images.small" alt="" />
     <strong class="searchResultitle">{{item.title}}</strong>
     <div id="ratingStar">
        <star :score="item.rating.average" class="star"></star>
       <i class="ratingScore">{{item.rating.average}}</i>  
      </div>
     </a>
    </li>
    </ul>
     <span id="searchMore"></span>
    </div>
  </div>
  </div>  
</template>

<script>
  import jsonp from 'jsonp'
  import Vue from 'vue'
  import Star from '@/components/Ratingstar/Star'

  export default {
    name: 'teris',
    data () {
       return {
       data:{},
       searchName:"",
       score:{}
      }
       
    },
    components:{
      star:Star
    },
    create () {
      this.getMovie()
    },
    methods: {
    
      Menter:function () {
          self=this
        jsonp('https://api.douban.com/v2/movie/search?q='+this.searchName, null, function (err, data) {
          if (err) {
            console.error(err.message)

          } else {
            
              self.data=data.subjects
              
            
          }
        })
      },
      getMovie(){
         var s=$(".ratingScore").text()
         this.score=Math.ceil(s/2)
         console.log(this.score)

      }
     
     
    }
  }

 
</script>


<style>
.searchHeader{
  width:100%;
  height:4rem;
  float:left;
  border-top:.5px solid #ebe4e4;
   border-bottom:.5px solid #ebe4e4;
}
#demo{
  width:80%;
  height:3rem;
  margin-left:10%;
  margin-top:0.5rem;
  box-sizing:border-box;
 
  
}
.searchName{
  width:80%;
  height:90%;
   background-color:#f5f5f5;

}
.searchResult{
  width:100%;
  height:5rem;
}
.searchResult ul{
  width:100%;
 
  
}
.searchResult ul>li{
  width:100%;
  height:5rem;
  float:left;
  padding:.5rem 0;
  border-bottom:.5px solid #f5f5f5;
  box-sizing:border-box;
}
.searchResult ul>li>a{
  display:block;
  width:80%;
  height:100%;
  margin-left:10%;
  box-sizing:border-box;
}
.searchResult ul>li>a>img{
  height:100%;
  width:4rem;
  float:left;
}
.searchResultitle{
display:block;
width:80%;
float:left;
margin-left:1rem;
font-size:1.6rem;
color:black;
overflow:hidden;
text-align:left;
}
.Ratingstar{
  color:orange;
  float:left;
  margin-left:1rem;
}
.searchMore{
  width:100%;
  height:2rem;
  float:left;
}
.star{
  float:left;
  margin-left:1rem;
  margin-top:.5rem;
}
</style>


