import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/view/home/index'
import Movie from '@/view/movie/index'
import Searchmovie from '@/view/search/movie'
import Searchmusic from '@/view/search/music'
import Search from '@/view/search/index'
import Moremovie from '@/view/movie/index1'

Vue.use(Router)


export default new Router({
    routes: [{
            path: '/home',
            name: 'Home',
            component: Home
        }, {
            path: '/movie',
            name: 'Movie',
            component: Movie
        }, {
            path: '/searchmovie',
            name: 'Searchmovie',
            component: Searchmovie
        },
        {
            path: '/searchmusic',
            name: 'Searchmusic',
            component: Searchmusic
        }, {
            path: '/search',
            name: 'Search',
            component: Search
        },
        {
            path: '/moremovie',
            name: 'Moremovie',
            component: Moremovie
        }

    ]
})


