
  <template>
  <swiper :options="swiperOption" ref="mySwiper">
    <!-- slides -->
    <swiper-slide v-for="item in newsubject" :key="item.id" style="border:none;" id="myList">
    <img v-bind:src="item.images.medium" alt=""/>
    <span class="movieTitle">{{item.title}}</span>
    <span class="movieRating">:<i>{{item.rating.average}}</i></span>
    </swiper-slide>
    
    <!-- Optional controls -->
     <div class="swiper-pagination"  slot="pagination"></div>
    <div class="swiper-button-prev" slot="button-prev"></div>
    <div class="swiper-button-next" slot="button-next"></div>
    <div class="swiper-scrollbar"   slot="scrollbar"></div>
  </swiper>
</template>
 
<script>
  import jsonp from 'jsonp'
  import Vue from 'vue'
import VueAwesomeSwiper from 'vue-awesome-swiper'

Vue.use(VueAwesomeSwiper) 

  export default {
    name: 'carrousel',
    data() {
      return {
        swiperOption: {
          notNextTick: true,
          autoplay: 3000,
          direction : 'horizontal',
          grabCursor : true,
          setWrapperSize :true,
          autoHeight: true,
          pagination : '.newswiper',
          paginationClickable :true,
          prevButton:'.swiper-button-prev',
          nextButton:'.swiper-button-next',
          scrollbar:'.swiper-scrollbar',
          mousewheelControl : true,
          observeParents:true,
          slidesPerView : 3,
          debugger: true,
          spaceBetween : 10,
          onTransitionStart(swiper){
          }    
        },
         newsubject:" "
         
      }
    },
    
    computed: {
      swiper() {
        return this.$refs.mySwiper.swiper
      }
    },
    mounted() {
      this.swiper.slideTo(3, 1000, false)
    },
     created () {
      this.getJsonp()
     
    },
     methods: {
      getJsonp () {
           self=this
        jsonp('https://api.douban.com/v2/movie/in_theaters', null, function (err, data) {
          if (err) {
            console.error(err.message)
          } else {
         
              self.newsubject=data.subjects
           
              console.log(self.newsubject)
            }
               
             
        })
      }
     
    }
  }


  
</script>

<style>
.swiper-slide img{
  width:90%;
  height:10rem;
  
}
.swiper-slide{
  float:left;
  
  
}
.movieTitle{
  display:block;
  width:100%;
  height:2rem;
  white-space:nowrap;
  text-overflow:ellipsis;
  overflow:hidden;
}
.movieRating{
  display:block;
  width:100%;
  height:2.5rem;
 
}
</style>


