<template>
  <swiper :options="swiperOption" ref="mySwiper" class="swiper1">
    <!-- slides -->
    <swiper-slide><a>IMDB250250</a> </swiper-slide>
    <swiper-slide><a></a> </swiper-slide>
    <swiper-slide><a></a> </swiper-slide>
    <swiper-slide><a></a> </swiper-slide>
  </swiper>


  
</template>
 
<script>
  import jsonp from 'jsonp'
  import Vue from 'vue'

  export default {
    name: 'carrousel1',
    data() {
      return {
        swiperOption: {
          width:500,
          height:50,
          notNextTick: true,
          autoplay: 3000,
          direction : 'horizontal',
          grabCursor : true,
          setWrapperSize :true,
          autoHeight: true,
          paginationClickable :true,
          mousewheelControl : true,
          observeParents:true,
          slidesPerView : 2,
          debugger: true,
          spaceBetween : 20,
          onTransitionStart(swiper){
          },    
        }
        
      }
    },
    
    computed: {
      swiper() {
        return this.$refs.mySwiper.swiper
      }
    },
    mounted() {
      this.swiper.slideTo(3, 1000, false)
    }
    
  }


  
</script>

<style>
..swiper-slide a{
  display:block;
  height:3rem;
  
}
.swiper-slide{
  float:left;
   border:1px solid pink;
}
</style>


