<template>
<div>
  <div>
    <hotmovie></hotmovie>
    <hswiper></hswiper>
  </div>
  <div>
   <findmovie></findmovie>
   <findswiper1></findswiper1>
   <findswiper2></findswiper2>
  </div>
  <div>
  <sortkind></sortkind>
  <sortable></sortable>
  </div>
  <div>
  <download></download>
  </div>
</div>
</template>

<script>
import Vue from 'vue'
import VueAwesomeSwiper from 'vue-awesome-swiper'

Vue.use(VueAwesomeSwiper) 

import HSwiper from '@/components/movie/Hotswiper'
import Hotmovie from '@/components/movie/Hotmovie'
import Findmovie from '@/components/movie/Findmovie'
import Findswiper1 from '@/components/movie/Findswiper1'
import Findswiper2 from '@/components/movie/Findswiper2'
import Sortkind from '@/components/movie/Sortkind'
import Sortable from '@/components/movie/Sortable'
import Download from '@/components/movie/Download'

  export default({
    name:"app",
    data(){
      return{
        name:'teris'
      }
    },
    components:{
      hswiper:HSwiper,
      hotmovie:Hotmovie,
      findmovie:Findmovie,
      findswiper1:Findswiper1,
      findswiper2:Findswiper2,
      sortkind:Sortkind,
      sortable:Sortable,
      download:Download
    }

  })

</script>


